import * as THREE from 'three';
import { GroupBase } from '../../bases';
import DIFFUSE from './textures/paper_diffuse.jpg';
import DISPLACE from './textures/paper_displace.jpg';
import NORMAL from './textures/paper_normal.jpg';
import ROUGH from './textures/paper_rough.jpg';
const MIN_FOLDED_HEIGHT = 0.025;
const MIN_RADIUS = 0.03;
const MID_CREASE_FACTOR = 1 / 2;
const TOUCHDOWN_FACTOR = 1 / 8;
export class Paper extends GroupBase {
    constructor(parentState) {
        super(parentState);
        // Set up paper geometry
        const paperGeometry = new THREE.PlaneBufferGeometry(11, 8, 220, 160);
        paperGeometry.rotateX(-Math.PI / 2);
        paperGeometry.center();
        const positionAttribute = paperGeometry.attributes
            .position;
        positionAttribute.setUsage(THREE.DynamicDrawUsage);
        const coordinateAttribute = positionAttribute.clone();
        paperGeometry.setAttribute('coordinate', coordinateAttribute);
        const originalsAttribute = positionAttribute.clone();
        paperGeometry.setAttribute('originals', originalsAttribute);
        const pinnedAttribute = new THREE.BufferAttribute(new Float32Array(Math.floor(positionAttribute.array.length / 3)), 1, false);
        paperGeometry.setAttribute('pinned', pinnedAttribute);
        // Set up paper material
        const topMaterial = new THREE.MeshStandardMaterial({
            color: 0xeeeeee,
            side: THREE.FrontSide,
            envMapIntensity: 1,
            displacementScale: 0.02,
            displacementBias: -0.015,
            metalness: 0,
            roughness: 1,
        });
        topMaterial.normalScale.set(0.15, 0.15);
        const loader = new THREE.TextureLoader();
        topMaterial.map = loader.load(DIFFUSE);
        topMaterial.normalMap = loader.load(NORMAL);
        topMaterial.displacementMap = loader.load(DISPLACE);
        topMaterial.roughnessMap = loader.load(ROUGH);
        topMaterial.map.wrapS = THREE.RepeatWrapping;
        topMaterial.map.repeat.set(1, 1);
        topMaterial.normalMap.wrapS = THREE.RepeatWrapping;
        topMaterial.normalMap.repeat.set(1, 1);
        topMaterial.roughnessMap.wrapS = THREE.RepeatWrapping;
        topMaterial.roughnessMap.repeat.set(1, 1);
        topMaterial.displacementMap.wrapS = THREE.RepeatWrapping;
        topMaterial.displacementMap.repeat.set(1, 1);
        // Duplicate material for different top / bottom
        const bottomMaterial = topMaterial.clone();
        bottomMaterial.side = THREE.BackSide;
        bottomMaterial.color = new THREE.Color(0x3b186f);
        topMaterial.normalScale.set(0.07, 0.07);
        bottomMaterial.roughness = 0.75;
        const paperTopMesh = new THREE.Mesh(paperGeometry, topMaterial);
        const paperBottomMesh = new THREE.Mesh(paperGeometry, bottomMaterial);
        paperTopMesh.castShadow = true;
        paperBottomMesh.castShadow = true;
        paperTopMesh.receiveShadow = true;
        paperBottomMesh.receiveShadow = true;
        // Set up outlines
        const edgesGeometry = new THREE.EdgesGeometry(paperGeometry);
        const lineMaterial = new THREE.LineBasicMaterial({ color: 0x444444 });
        const outlineMesh = new THREE.LineSegments(edgesGeometry, lineMaterial);
        const linesPositionAttribute = edgesGeometry.attributes
            .position;
        linesPositionAttribute.setUsage(THREE.DynamicDrawUsage);
        const linesCoordinateAttribute = linesPositionAttribute.clone();
        edgesGeometry.setAttribute('coordinate', linesCoordinateAttribute);
        const linesOriginalsAttribute = linesPositionAttribute.clone();
        edgesGeometry.setAttribute('originals', linesOriginalsAttribute);
        const linesPinnedAttribute = new THREE.BufferAttribute(new Float32Array(Math.floor(linesPositionAttribute.array.length / 3)), 1, false);
        edgesGeometry.setAttribute('pinned', linesPinnedAttribute);
        // Set up dummy intersection
        const intersectionGeometry = new THREE.PlaneBufferGeometry(110, 80, 1, 1);
        intersectionGeometry.rotateX(-Math.PI / 2);
        intersectionGeometry.center();
        const intersectionMaterialTop = new THREE.MeshBasicMaterial({
            visible: false,
            side: THREE.FrontSide,
        });
        const intersectionMaterialBottom = new THREE.MeshBasicMaterial({
            visible: false,
            side: THREE.BackSide,
        });
        const intersectionMeshTop = new THREE.Mesh(intersectionGeometry, intersectionMaterialTop);
        const intersectionMeshBottom = new THREE.Mesh(intersectionGeometry, intersectionMaterialBottom);
        // Add meshes
        this.add(paperTopMesh, paperBottomMesh, outlineMesh, intersectionMeshTop, intersectionMeshBottom);
        this.state.disposalList.push(paperGeometry, edgesGeometry, topMaterial, topMaterial.map, topMaterial.normalMap, topMaterial.displacementMap, topMaterial.roughnessMap, bottomMaterial, lineMaterial, intersectionGeometry, intersectionMaterialTop, intersectionMaterialBottom);
        this.state = Object.assign(Object.assign({}, this.state), { raycaster: this.state.scene.getRaycaster(), target: new THREE.Vector2(), L: 5.5, shift: 0, targetAngle: 0, foldCount: 0, currentRadius: 0, currentFlatBottomLength: 0, currentAngle: 0, currentShift: 0, isFoldMode: true, directionFlag: 1, intersectionMeshTop,
            intersectionMeshBottom,
            paperGeometry,
            edgesGeometry,
            paperTopMesh,
            paperBottomMesh,
            outlineMesh });
    }
    startFold(foldTarget, mouseAnchor) {
        this.state.L = foldTarget.distanceTo(mouseAnchor);
        this.state.shift = foldTarget.x;
        const deltaV = new THREE.Vector2().subVectors(mouseAnchor, foldTarget);
        this.state.targetAngle = Math.atan2(deltaV.y, deltaV.x);
        this.state.foldCount += 1;
        this.state.scene.addToUpdateList('fold', () => {
            this.stepFold();
        });
    }
    endFold() {
        this.state.scene.removeFromUpdateList('fold');
    }
    startCrease(callback) {
        if (!this.state.isFoldMode) {
            callback();
            return;
        }
        this.state.scene.addToUpdateList('crease', () => {
            this.stepCrease(callback);
        });
    }
    pinVertices(isPinned) {
        this.pinVerticesHelper(this.state.paperGeometry, isPinned);
        this.pinVerticesHelper(this.state.edgesGeometry, isPinned);
    }
    setBendMode() {
        this.state.isFoldMode = false;
    }
    setFoldMode() {
        this.state.isFoldMode = true;
    }
    isMouseOverPaper() {
        const intersections = this.state.raycaster.intersectObjects([
            this.state.paperTopMesh,
            this.state.paperBottomMesh,
        ]);
        return intersections.length > 0;
    }
    rotatePaper(rx, ry, rz) {
        this.rotateX(rx);
        this.rotateY(ry);
        this.rotateZ(rz);
    }
    pinVerticesHelper(geometry, isPinned) {
        const originalsArray = geometry.attributes.originals.array;
        const pinnedArray = geometry.attributes.pinned.array;
        for (let i = 0; i < originalsArray.length; i += 3) {
            pinnedArray[Math.floor(i / 3)] = isPinned(originalsArray[i], originalsArray[i + 1], originalsArray[i + 2])
                ? 1
                : 0;
        }
    }
    updateTarget() {
        const [topIntersection] = this.state.raycaster.intersectObject(this.state.intersectionMeshTop);
        if (!topIntersection) {
            const [bottomIntersection] = this.state.raycaster.intersectObject(this.state.intersectionMeshBottom);
            if (!bottomIntersection) {
                return;
            }
            this.state.target.set((bottomIntersection.uv.x - 0.5) * 110, (bottomIntersection.uv.y - 0.5) * 80);
            this.state.directionFlag = -1;
            return;
        }
        this.state.target.set((topIntersection.uv.x - 0.5) * 110, (topIntersection.uv.y - 0.5) * 80);
        this.state.directionFlag = 1;
    }
    stepFold() {
        const oldTarget = this.state.target.clone();
        const oldDirection = this.state.directionFlag;
        this.updateTarget();
        if (!this.state.target.equals(oldTarget) ||
            oldDirection !== this.state.directionFlag) {
            if (this.state.isFoldMode) {
                this.foldGeometry();
            }
            else {
                this.bendGeometry();
            }
        }
    }
    stepCrease(callback) {
        const oldTarget = this.state.target.clone();
        const oldDirection = this.state.directionFlag;
        this.updateTarget();
        if (!this.state.target.equals(oldTarget) ||
            oldDirection !== this.state.directionFlag) {
            const isDone = this.creaseGeometry();
            if (isDone) {
                this.state.scene.removeFromUpdateList('crease');
                this.recomputeCoordinates(this.state.paperGeometry);
                this.recomputeCoordinates(this.state.edgesGeometry);
                callback === null || callback === void 0 ? void 0 : callback();
            }
        }
    }
    recomputeCoordinates(geometry) {
        const positionArray = geometry.attributes.position.array;
        const coordinateArray = geometry.attributes.coordinate
            .array;
        for (let i = 0; i < coordinateArray.length; i += 3) {
            coordinateArray[i] = positionArray[i];
            coordinateArray[i + 1] = positionArray[i + 1];
            coordinateArray[i + 2] = positionArray[i + 2];
        }
    }
    creaseGeometry() {
        const { currentRadius, currentFlatBottomLength, currentAngle, currentShift, paperGeometry, edgesGeometry, } = this.state;
        if (currentRadius < MIN_FOLDED_HEIGHT) {
            return true;
        }
        const target = this.toUV(-currentAngle, this.state.target.x, this.state.target.y, currentShift)[0];
        const newlyFlatTop = Math.max(target - (currentFlatBottomLength - 2 * currentRadius), 0);
        const newRadius = Math.max(currentRadius - (2 * newlyFlatTop) / (2 + 2 * Math.PI), 0);
        let result;
        result = this.applyParametricFold(paperGeometry, currentFlatBottomLength -
            2 * currentRadius +
            newlyFlatTop +
            2 * newRadius, newRadius, currentAngle, currentShift);
        result && (result = this.applyParametricFold(edgesGeometry, currentFlatBottomLength -
            2 * currentRadius +
            newlyFlatTop +
            2 * newRadius, newRadius, currentAngle, currentShift));
        if (result) {
            paperGeometry.attributes.position.needsUpdate = true;
            paperGeometry.computeBoundingBox();
            paperGeometry.computeVertexNormals();
            paperGeometry.computeBoundingSphere();
            edgesGeometry.attributes.position.needsUpdate = true;
            edgesGeometry.computeBoundingBox();
            edgesGeometry.computeVertexNormals();
            edgesGeometry.computeBoundingSphere();
        }
        return newRadius < MIN_FOLDED_HEIGHT;
    }
    bendGeometry() {
        const { L, shift, targetAngle, edgesGeometry, paperGeometry } = this.state;
        const midCrease = L * MID_CREASE_FACTOR;
        const [offsetU, offsetV] = this.toUV(targetAngle, this.state.target.x, this.state.target.y, shift);
        const targetT = Math.max(Math.min((L - offsetU - midCrease / 2) / (2 * L), 1), 0);
        const offsetAngle = targetT * Math.atan2(offsetV, L - offsetU);
        const target = offsetU < L
            ? targetT *
                (L - Math.sqrt(Math.pow((L - offsetU), 2) + Math.pow((2 * offsetV), 2))) +
                offsetU * (1 - targetT)
            : offsetU;
        const angle = -(targetAngle + offsetAngle);
        this.applyParametricBend(paperGeometry, target, midCrease, angle, shift);
        this.applyParametricBend(edgesGeometry, target, midCrease, angle, shift);
        paperGeometry.attributes.position.needsUpdate = true;
        paperGeometry.computeBoundingBox();
        paperGeometry.computeVertexNormals();
        paperGeometry.computeBoundingSphere();
        edgesGeometry.attributes.position.needsUpdate = true;
        edgesGeometry.computeBoundingBox();
        edgesGeometry.computeVertexNormals();
        edgesGeometry.computeBoundingSphere();
    }
    applyParametricBend(geometry, target, hypotenuse, angle, shift) {
        const positionArray = geometry.attributes.position
            .array;
        const coordinateArray = geometry.attributes.coordinate.array;
        const pinnedArray = geometry.attributes.pinned.array;
        const theta = Math.min(Math.acos(Math.max(Math.min(target - hypotenuse, hypotenuse), -hypotenuse) / hypotenuse), Math.PI - MIN_RADIUS);
        for (let i = 0; i < positionArray.length; i += 3) {
            if (pinnedArray[Math.floor(i / 3)] > 0) {
                continue;
            }
            const [u, v] = this.toUV(angle, coordinateArray[i], coordinateArray[i + 2], shift);
            const y = coordinateArray[i + 1] * this.state.directionFlag;
            if (u > hypotenuse) {
                const t = (u - hypotenuse) / (hypotenuse + 0.00001);
                const [x, z] = this.fromUV(angle, hypotenuse * t * Math.cos(theta) -
                    (y * Math.sin(theta)) / 8 +
                    hypotenuse, v, shift);
                positionArray[i] = x;
                positionArray[i + 2] = z;
                positionArray[i + 1] =
                    hypotenuse * t * Math.sin(theta) +
                        (y * Math.cos(theta)) / 8 +
                        Math.abs(2 *
                            MIN_FOLDED_HEIGHT *
                            this.state.foldCount *
                            Math.cos(theta));
            }
            else {
                positionArray[i] = coordinateArray[i];
                positionArray[i + 2] = coordinateArray[i + 2];
                positionArray[i + 1] = y;
            }
            positionArray[i + 1] *= this.state.directionFlag;
        }
        this.state.currentRadius = 0;
        this.state.currentFlatBottomLength = 0;
        this.state.currentAngle = angle;
        this.state.currentShift = shift;
    }
    foldGeometry() {
        const { L, shift, targetAngle, edgesGeometry, paperGeometry } = this.state;
        const midCrease = L * MID_CREASE_FACTOR;
        const touchdownPoint = L * TOUCHDOWN_FACTOR;
        const [offsetU, offsetV] = this.toUV(targetAngle, this.state.target.x, this.state.target.y, shift);
        const targetT = Math.max(Math.min((L - offsetU - midCrease / 2) / (2 * L), 1), 0);
        const offsetAngle = targetT * Math.atan2(offsetV, L - offsetU);
        const target = offsetU < L
            ? targetT *
                (L - Math.sqrt(Math.pow((L - offsetU), 2) + Math.pow((2 * offsetV), 2))) +
                offsetU * (1 - targetT)
            : offsetU;
        const angle = -(targetAngle + offsetAngle);
        let result;
        if (target > L) {
            result = this.applyParametricFold(paperGeometry, target, Math.pow(4, target / L), -targetAngle, shift);
            result && (result = this.applyParametricFold(edgesGeometry, target, Math.pow(4, target / L), -targetAngle, shift));
        }
        else if (target > midCrease) {
            const t = (target - midCrease) / midCrease;
            const r = (L / (2 * Math.PI)) * Math.pow(4, t);
            result = this.applyParametricFold(paperGeometry, target, r, angle, shift);
            result && (result = this.applyParametricFold(edgesGeometry, target, r, angle, shift));
        }
        else if (target > touchdownPoint) {
            const t = (target - touchdownPoint) / (midCrease - touchdownPoint);
            const tPrime = Math.pow(t, 2);
            const r = (tPrime * L) / (2 * Math.PI) +
                ((1 - tPrime) * (L - touchdownPoint)) / (2 + 2 * Math.PI);
            const arcLength = Math.PI * r;
            const foldLength = (1 - t) * arcLength;
            const flatBottomLength = L - arcLength - foldLength;
            result = this.applyParametricFold(paperGeometry, flatBottomLength, r, angle, shift);
            result && (result = this.applyParametricFold(edgesGeometry, flatBottomLength, r, angle, shift));
        }
        else {
            const adjustedTarget = Math.max(target, -L);
            const t = 1 - (adjustedTarget - touchdownPoint) / (-L - touchdownPoint);
            const r = (t * (L - touchdownPoint)) / (2 + 2 * Math.PI) +
                (1 - t) * MIN_RADIUS;
            const arcLength = Math.PI * r;
            const flatBottomLength = Math.max((L + 2 * r + adjustedTarget) / 2 - arcLength, 0);
            result = this.applyParametricFold(paperGeometry, flatBottomLength, r, angle, shift);
            result && (result = this.applyParametricFold(edgesGeometry, flatBottomLength, r, angle, shift));
        }
        if (result) {
            paperGeometry.attributes.position.needsUpdate = true;
            paperGeometry.computeBoundingBox();
            paperGeometry.computeVertexNormals();
            paperGeometry.computeBoundingSphere();
            edgesGeometry.attributes.position.needsUpdate = true;
            edgesGeometry.computeBoundingBox();
            edgesGeometry.computeVertexNormals();
            edgesGeometry.computeBoundingSphere();
        }
    }
    applyParametricFold(geometry, flatBottomLength, radius, angle, shift) {
        const positionArray = geometry.attributes.position
            .array;
        const coordinateArray = geometry.attributes.coordinate.array;
        const pinnedArray = geometry.attributes.pinned.array;
        const arcLength = radius * Math.PI;
        const flatTopStartCoordinate = flatBottomLength + 2 * arcLength;
        const flatTopStartPosition = flatBottomLength - 2 * radius;
        for (let i = 0; i < positionArray.length; i += 3) {
            if (pinnedArray[Math.floor(i / 3)] > 0) {
                continue;
            }
            const [u, v] = this.toUV(angle, coordinateArray[i], coordinateArray[i + 2], shift);
            const y = coordinateArray[i + 1] * this.state.directionFlag;
            if (y > radius / 2 && y > 8 * MIN_RADIUS && u > flatBottomLength) {
                return false;
            }
            if (u > flatTopStartCoordinate) {
                const [x, z] = this.fromUV(angle, flatTopStartPosition - (u - flatTopStartCoordinate), v, shift);
                positionArray[i] = x;
                positionArray[i + 2] = z;
                positionArray[i + 1] =
                    this.state.foldCount * MIN_FOLDED_HEIGHT - y / 8;
            }
            else if (u > flatBottomLength + arcLength) {
                const foldT = 1 - (flatTopStartCoordinate - u) / arcLength;
                const theta = foldT * Math.PI;
                if (theta < Math.PI / 2) {
                    const [x, z] = this.fromUV(angle, flatBottomLength - Math.sin(theta) * (radius - y), v, shift);
                    positionArray[i] = x;
                    positionArray[i + 2] = z;
                    positionArray[i + 1] =
                        radius + Math.cos(theta) * (radius - y);
                }
                else {
                    const [x, z] = this.fromUV(angle, flatBottomLength -
                        2 * radius +
                        Math.sin(theta) * (radius + y), v, shift);
                    positionArray[i] = x;
                    positionArray[i + 2] = z;
                    positionArray[i + 1] = Math.max(radius + Math.cos(theta) * (radius + y), this.state.foldCount * MIN_FOLDED_HEIGHT - y / 8);
                }
            }
            else if (u > flatBottomLength) {
                const theta = (Math.PI * (u - flatBottomLength)) / arcLength;
                const [x, z] = this.fromUV(angle, flatBottomLength + Math.sin(theta) * (radius - y), v, shift);
                positionArray[i] = x;
                positionArray[i + 2] = z;
                positionArray[i + 1] = radius - Math.cos(theta) * (radius - y);
            }
            else {
                positionArray[i] = coordinateArray[i];
                positionArray[i + 2] = coordinateArray[i + 2];
                positionArray[i + 1] = y;
            }
            positionArray[i + 1] *= this.state.directionFlag;
        }
        this.state.currentRadius = radius;
        this.state.currentFlatBottomLength = flatBottomLength;
        this.state.currentAngle = angle;
        this.state.currentShift = shift;
        return true;
    }
    toUV(angle, x, z, shift) {
        return [
            (x - shift) * Math.cos(angle) - z * Math.sin(angle),
            (x - shift) * Math.sin(angle) + z * Math.cos(angle),
        ];
    }
    fromUV(angle, u, v, shift) {
        return [
            u * Math.cos(-angle) - v * Math.sin(-angle) + shift,
            u * Math.sin(-angle) + v * Math.cos(-angle),
        ];
    }
}
