import * as THREE from 'three';
import { GroupBase } from '../bases';
import { Paper } from '../objects/Paper';
export class FoldingController extends GroupBase {
    constructor(parentState) {
        super(parentState);
        const paper = new Paper(this.state);
        this.addGroup(paper);
        this.position.set(0.7, -0.05, 0.3);
        this.rotateY(-0.02);
        const clickHandler = () => this.onClick();
        document.addEventListener('click', clickHandler);
        this.state.disposalList.push({
            dispose: () => document.removeEventListener('click', clickHandler),
        });
        this.state = Object.assign(Object.assign({}, this.state), { isFolding: false, isCreasing: false, currentStep: 0, paper });
    }
    onClick() {
        const { isFolding, isCreasing, currentStep, paper } = this.state;
        if (isFolding) {
            paper.endFold();
            this.state.isCreasing = true;
            this.state.isFolding = false;
            this.state.currentStep += 1;
            paper.startCrease(() => {
                this.state.isCreasing = false;
                const callback = this.getAfterStepCallback(currentStep);
                callback();
            });
        }
        else if (!isCreasing && this.state.currentStep < 7) {
            if (!paper.isMouseOverPaper()) {
                return;
            }
            paper.startFold(...this.getFoldSettingsFromStep(currentStep));
            this.state.isFolding = true;
        }
    }
    getAfterStepCallback(currentStep) {
        switch (currentStep) {
            case 0:
            case 1:
            case 2:
            case 3:
                return () => { };
            case 4:
                return () => this.state.paper.rotatePaper(-Math.PI / 2, 0, 0);
            case 5:
                return () => { };
            case 6:
                return () => this.state.paper.rotatePaper(0, 0, -Math.PI / 4);
            default:
                return () => { };
        }
    }
    getFoldSettingsFromStep(currentStep) {
        switch (currentStep) {
            case 0:
                return [new THREE.Vector2(1.5, 0), new THREE.Vector2(5.5, 4)];
            case 1:
                return [new THREE.Vector2(1.5, 0), new THREE.Vector2(5.5, -4)];
            case 2:
                return [new THREE.Vector2(0, 0), new THREE.Vector2(1.5, 4)];
            case 3:
                return [new THREE.Vector2(0, 0), new THREE.Vector2(1.5, -4)];
            case 4:
                return [new THREE.Vector2(0, 0), new THREE.Vector2(-1.5, -4)];
            case 5:
                this.state.paper.pinVertices((x, y, z) => z > 0);
                this.state.paper.setBendMode();
                return [new THREE.Vector2(1.5, 0), new THREE.Vector2(1.5, 2)];
            case 6:
            default:
                this.state.paper.pinVertices((x, y, z) => z < 0);
                this.state.paper.setBendMode();
                return [new THREE.Vector2(1.5, 0), new THREE.Vector2(1.5, 2)];
        }
    }
}
